.promo-slider {
  user-select: none;
  cursor: grab;
  width: 100vw;
  height: 212px;
  overflow: hidden;
  border-top: 1px solid #dfdfe1;
  border-bottom: 1px solid #dfdfe1;
  transform: translate3d(0%, 0px, 0px);
  transition: translate3d 0.2s ease-out;
  position: relative;
}

.slides-container {
  height: 100%;
  display: flex;
  gap: 0;
}

.slide {
  height: 100%;
  width: 100vw;
  min-width: 100%;
  overflow: hidden;
  cursor: pointer;
  color: purple;
  font-size: 5rem;
  text-align: center;
  padding: 2rem;
}

.slide1 {
  background-color: var(--color-ex-1);
}

.slide2 {
  background-color: var(--color-ex-2);
}

.slide3 {
  background-color: var(--color-ex-3);
}

.slide4 {
  background-color: var(--color-ex-4);
}

.slide5 {
  background-color: var(--color-ex-5);
}

.slide6 {
